.main-header {
  padding-top: 34px;
  padding-bottom: 26px;

  @include vp-1279 {
    padding-top: 15px;
  }

  @include vp-767 {
    padding-top: 22px;
    padding-bottom: 22px;
  }
}

.main-header__title {
  @include font-values(500, 36px, 1.52, $color-default-black);
  text-transform: uppercase;
  text-align: center;

  margin: 0;

  @include vp-767 {
    font-size: 24px;
  }
}
